<template>
  <div class="container">
    <div class="section__ind Main">
      <TopBar />
      <el-header class="ind">
        <h3>Регистрация</h3>
      </el-header>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="120px"
        class="demo-ruleForm"
      >
        <el-form-item label="Имя" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="Емейл" prop="email">
          <el-input v-model="ruleForm.email"></el-input>
        </el-form-item>
        <el-form-item label="Пароль" prop="pass">
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >Зарегистрироваться</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import TopBar from "@/components/TopBar";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "new",
  components: {
    TopBar,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("Введите пароль"));
      } else if (value.length < 6) {
        callback(new Error("Пароль должен состоять минимум из 6 символов"));
      } else if (!/^\s*(\w+)\s*$/.test(value)) {
        callback(new Error("Пароль не должен содержать киррилицу!"));
      } else {
        callback();
      }
    };
    return {
      ruleForm: {
        email: "",
        pass: "",
        name: "",
      },
      rules: {
        email: [
          {
            required: true,
            message: "Пожалуйста, введите адрес электронной почты",
            trigger: "blur",
          },
          {
            type: "email",
            message: "Пожалуйста, введите правильный адрес электронной почты",
            trigger: ["blur", "change"],
          },
        ],
        pass: [
          {
            required: true,
            validator: validatePass,
            trigger: ["blur", "change"],
          },
        ],
        name: [
          {
            required: true,
            message: "Пожалуйста, введите имя",
            trigger: ["blur", "change"],
          },
        ],
      },
    };
  },

  computed: {
    ...mapGetters("general", ["getStatusUser"]),
  },
  methods: {
    ...mapActions("general", ["setStatusUser"]),
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.setStatusUser(!this.getStatusUser);
          this.resetForm("ruleForm");
          this.$router.push({ name: "Main" });
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style>
.ind {
  margin-bottom: 30px;
}
</style>
