import Vue from 'vue';


const general = {
	namespaced: true,
	state: {
		statusUser: false,
	},
	getters: {
		getStatusUser(state) {
			return state.statusUser;
		}
	},
	mutations: {
		ADD_NEW_STATUS_USER(state, status) {
			state.statusUser = status;
		}
	},
	actions: {
		setStatusUser(context, status) {
			context.commit('ADD_NEW_STATUS_USER', status);
		}
	}
};

export default general;